@import 'variables/palette';
@import 'theme/grid';

$xs-screen: map-get($break-points, xs);
$sm-screen: map-get($break-points, sm);
$md-screen: map-get($break-points, md);
$lg-screen: map-get($break-points, lg);

.Carousel {
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;

  .Carousel__wrapper {
    width: 100%;
    overflow: hidden;
    position: relative;
    background-color: $brand-white;
  }

  .Carousel__slider {
    width: 100%;
    display: flex;
    height: 100%;
    position: relative;

    &--single {
      .Carousel__tile {
        overflow: hidden;
        width: 100%;
        flex-shrink: 0;
        background-color: $canvas-gray-500;
        cursor: grab;

        &--fade {
          visibility: hidden;
          opacity: 0;
          transition: opacity 500ms ease-in;
        }
      }

      &--loop {
        transform: translateX(-100%);
      }

      &--loopSingleChild {
        transform: none;
      }

      &--regular {
        transition: 0.5s;
        transform: translateX(0%);
      }

      &--fade {
        transform: translateX(0%);
      }
    }
    &--multi {
      .Carousel__tile {
        overflow: hidden;
        width: 55%;
        flex-shrink: 0;
        margin-right: 1.2rem;
        background-color: $canvas-gray-500;
        cursor: grab;

        @media (min-width: map-get($md-screen, min-width)) {
          width: 73.7%;
          margin-right: 1rem;
        }

        @media (min-width: map-get($lg-screen, min-width)) {
          width: 73.7%;
          margin-right: 1rem;
        }
      }

      &--loop {
        transform: translateX(-35%);

        @media (min-width: map-get($md-screen, min-width)) {
          transform: translateX(-61%);
        }

        @media (min-width: map-get($lg-screen, min-width)) {
          transform: translateX(-61%);
        }
      }

      &--loopSingleChild {
        transform: translateX(22%);

        @media (min-width: map-get($md-screen, min-width)) {
          transform: translateX(13.2%);
        }

        @media (min-width: map-get($lg-screen, min-width)) {
          transform: translateX(13.2%);
        }
      }

      &--regular {
        transition: 0.5s;
        transform: translateX(22%);
      }
    }
  }
}
