@import 'theme/grid';

$xs-screen: map-get($break-points, xs);
$sm-screen: map-get($break-points, sm);
$md-screen: map-get($break-points, md);
$lg-screen: map-get($break-points, lg);

.NavigationButtons {
  &__button {
    top: 50%;
    position: absolute;
    border-radius: 100%;
    transform: translateY(-50%);
    background-color: rgba(255, 255, 255, 0.4);
    border: 0;
    width: 4.8rem;
    height: 4.8rem;

    &--left {
      left: 1.4rem;
      padding-left: 0.2rem;

      @media (min-width: map-get($md-screen, min-width)) {
        left: 2.2rem;
      }

      @media (min-width: map-get($lg-screen, min-width)) {
        left: 2.2rem;
      }
    }

    &--right {
      right: 1.4rem;
      padding-right: 0.2rem;

      @media (min-width: map-get($md-screen, min-width)) {
        right: 2.2rem;
      }

      @media (min-width: map-get($lg-screen, min-width)) {
        right: 2.2rem;
      }
    }

    &:focus {
      border: 0.1rem solid $brand-white;
      outline: none;

      &:before {
        content: '';
        top: -0.3rem;
        left: -0.3rem;
        right: -0.3rem;
        bottom: -0.3rem;
        border: $canvas-dark-blue 0.2rem solid; 
        border-radius: 100%;
        position: absolute;
      }
    }
  }
}
