.date-picker-demo {
  display: flex;

  $controlPanelWidth: 30rem;
  $controlPanelPadding: 1.4rem;

  &__hr {
    position: relative;
    &:after {
      position: absolute;
      content: '';
      width: calc(100% + #{$controlPanelPadding} * 2);
      bottom: 0;
      left: -#{$controlPanelPadding};
      border-bottom: 0.1rem solid $canvas-gray-400;
    }
  }

  &__heading {
    width: 100%;
    line-height: 5rem;
    font-size: 2.4rem;
    margin-bottom: 2rem;

    @extend .date-picker-demo__hr;
  }

  &__group {
    @extend .date-picker-demo__hr;
  }

  &__subheading {
    font-size: 2rem;
    margin: 1.2rem 0;
  }

  &__control-panel {
    min-height: 100%;
    height: auto;
    border-right: 0.1rem solid $canvas-gray-400;
    padding: $controlPanelPadding;
    max-width: $controlPanelWidth;
    border-bottom: 0.1rem solid $canvas-gray-400;

    .Checkbox__label:first-child {
      padding-top: 0;
    }
  }

  &__control-calendar {
    .date-picker__text-fields-container {
      max-width: calc(#{$controlPanelWidth} - #{$controlPanelPadding} * 2);
      flex-direction: column;
    }

    .date-picker__text-field:first-child {
      margin-right: 0;
    }
  }

  &__demo-panel {
    position: relative;
    padding: $controlPanelPadding;
    flex-grow: 1;
    border-bottom: 0.1rem solid $canvas-gray-400;
  }

  &__code-area {
    position: absolute;
    left: 0;
    top: 73rem;
    width: 100%;
    background-color: #2c2c2e;
    padding: 2rem 3.2rem;
    min-height: calc(100% - 73rem);

    pre, code {
      font-family: monospace, monoce;
      font-size: 1.4rem;
      color: white;
    }

    pre {
      overflow: auto;
    }

    pre > code {
      display: block;
      word-wrap: normal;
    }
  }
}
