body {
  margin: 0;
  padding: 0;
  color: $brand-black;
}

table {
  width: 100%;
  font-size: 2.4rem;
  thead {
    td {
      border-bottom: .1rem $canvas-gray-400 solid;
      color: $brand-black;
      font-weight: bold;
      padding: .6rem;
    }
  }
  tr {
    td {
      margin: 2.4rem 0;
      padding: 1.2rem .6rem;
    }
  }
}

.spacing-8-top {
  padding-top: $spacing-8;
}

.gridDemo {
  color: white;
  border: solid 1px white;
  background-color: #757575;
  display: block;
  height: 30px;
}

.Home {

  &__header {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Scotia Headline', Arial, Helvetica, sans-serif;
    font-size: 2.4rem;
    font-weight: bold;
    height: 6.0rem;
    color: $brand-white;
    background: $brand-red;
  }

  &__menu {
    font-family: 'Scotia Regular', Arial, Helvetica, sans-serif;
    font-size: 2.4rem;

    li {
      height: 6.0rem;
      padding: 1.2rem 3.6rem;
      border-bottom: .1rem solid $canvas-gray-400;

      a {
        text-decoration: none;
      }
    }
  }
}

.IllustrativeIconDemo__reversed {
  background-color: $brand-black;
  padding: 0.5rem;
  width: fit-content;
}

.ListDemo {
  &__container {
    margin-left: 1.8rem;
  }
}

.overflow-x--Hidden {
  overflow-x: hidden;
}