.TableData {
  &__delBtn {
    background-color: transparent;
    border: 0;
  }

  &__link.Link__link {
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 1.4rem;
    line-height: 2.1rem;
  }

  &__amount {
    &::before {
      content: '$';
    }

    &::after {
      content: '.00';
    }
  }

  &__expenseRatio {
    text-align: end;
  }
}
