.Dots {
  padding-top: 1.8rem;
  list-style: none;
  text-align: center;
  line-height: 1;
 
  &__dot {
    display: inline-block;
    width: 0.8rem;
    height: 0.8rem;
    margin-left: 1.2rem;
    border: 0.1rem solid $brand-blue;
    border-radius: 50%;

    &--active {
      background: $brand-blue;
    }
  }

  :first-child {
    margin-left: 0;
  }
}
